import { Injectable, Inject, PLATFORM_ID } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { isPlatformBrowser } from '@angular/common';

@Injectable()
export class CookieService {
    private cookie$: Map<string, BehaviorSubject<CookieType>> = new Map<string, BehaviorSubject<CookieType>>();

    constructor(@Inject(PLATFORM_ID) private platformId: string) {
        if (isPlatformBrowser(this.platformId)) {
            this.listCookies().forEach(item => {
                if (!this.cookie$.has(item.name)) {
                    this.cookie$.set(item.name, new BehaviorSubject({ name: name, newValue: item.value }));
                } else {
                    const cookie$ = this.cookie$.get(item.name);
                    cookie$.next({ newValue: item.value, oldValue: cookie$.getValue() });
                }
            });
        }
    }

    public get(name): BehaviorSubject<CookieType> {
        if (!this.cookie$.has(name)) {
            const cookie$ = new BehaviorSubject({ newValue: undefined });
            this.cookie$.set(name, cookie$);
            return cookie$;
        } else {
            return this.cookie$.get(name);
        }
    }

    private listCookies(): Array<{ name: string, value: any }> {
        const theCookies = document.cookie.split(';');
        const aString = [];
        for (let i = 1; i <= theCookies.length; i++) {
            const record = theCookies[i - 1].split('=');
            aString.push({ name: record[0], value: record[1] });
        }
        return aString;
    }
    /**
    *
    * @param name          Name of the cookie to be returned
    * @return {string}     cookie value
    */
    public readCookie(name: string): string {
        const value = '; ' + document.cookie;
        const parts = value.split('; ' + name + '=');
        if (parts.length > 1) {
            return parts.pop().split(';').shift();
        }
        return undefined;
    }

    public setCookies(name: string, value: any, days: number = 0) {
        let expires = '';
        if (days) {
            const date = new Date();
            date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
            expires = ';expires=' + date.toUTCString();
        }
        /*if (!domainOfCookie)*/
        let newValue: string;
        if (!expires) {
            newValue = `${name}=${value}?;path=/`;
            document.cookie = `${name}=${value};path=/`;
        } else {
            newValue = `${name}=${value}?${expires}?;path=/`;
            document.cookie = `${name}=${value}${expires};path=/`;
        }
        const cookie$ = this.get(name);
        cookie$.next({ newValue: value, oldValue: cookie$.getValue() });
        return newValue;
    }

    public deleteCookie(name: string) {
        document.cookie = `${name}=;expires=Thu, 01 Jan 1970 00:00:00 UTC;path=/;`;
    }
}

export interface CookieType {
    newValue: any;
    oldValue?: any;
}
