declare let window: any;

import { Component, OnInit, Inject, PLATFORM_ID, Renderer2 } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { GlobalService } from '../app/shared/asyncServices/services/global.service';
import { CookieService } from './shared/asyncServices/services/cookie.service';
import { filter, map } from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html'
})
export class AppComponent implements OnInit {
  renderer = '';
  title = 'app';
  constructor(@Inject(PLATFORM_ID) private platformId: string, private globalService: GlobalService, public cookieService: CookieService,
    private renderer2: Renderer2) {
    cookieService.get('hostClass')
      .pipe(map(value => value.newValue))
      .subscribe(value => {
        if (value) {
          this.renderer2.addClass(document.body, value);
        } else {
          this.renderer2.removeClass(document.body, '_css_dark');
        }
      });
  }

  ngOnInit() {
    this.renderer = isPlatformBrowser(this.platformId) ? 'Browser' : 'Server';
  }

  initializeHostClass() {
  }
}
