import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
// import { LazyLoadImageModule } from 'ng-lazyload-image';
import { ROUTES } from './app.routes';

import { ServiceCore } from './shared/asyncServices/http/service.core';
import { GlobalService } from './shared/asyncServices/services/global.service';

import { AppComponent } from './app.component';
import { HomeComponent } from './pages/home/home.component';
import { CookieService } from './shared/asyncServices/services/cookie.service';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
  ],
  imports: [
    // LazyLoadImageModule,
    HttpModule,
    RouterModule.forRoot(ROUTES),
    BrowserModule.withServerTransition({ appId: 'angular-universal-fireba-75b35' })
  ],
  providers: [
    ServiceCore,
    GlobalService,
    CookieService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
